.tooltip-wrapper {
  display: inline-block;
  position: relative;
}

.tooltip {
  bottom: 28px;
  position: absolute;
  visibility: hidden;
  font-size: 12px;
  font-weight: 500;
  white-space: nowrap;
  background-color: #333333;
  padding: 2px 4px;
  border-radius: 4px;
  color: #ffffff;
}

.tooltip-trigger {
  display: flex;

  &:hover + .tooltip {
    visibility: visible;
  }
}


.single-prompt {

  .canvas {

    .disabled {
      pointer-events: none;
    }

    .drawing-controls {
      align-items: center;
      border: 1px #9c9c9c solid;
      border-top: none;
      border-radius: 0px 0px 4px 4px;
      display: flex;
      flex-direction: row;
      grid-gap: 8px;
      gap: 8px;
      padding: 8px;

      .separator {
        border-right: 1px #9c9c9c solid;
        height: 20px;
        margin: 0px 5px;
      }

      .single-color-picker {
        border-radius: 50%;
        cursor: pointer;
        height: 20px;
        margin: 2px;
        transition: all .25s ease;
        width: 20px;

        &.is-white {
          border: 1px black solid;
          /* Make a smaller circle so that the width and height don't change when border is added. */
          height: 18px;
          width: 18px;
        }

        &.selected {
          width: 24px;
          height: 24px;
          margin: 0px;

          &.is-white {
            /* Make a smaller circle so that the width and height don't change when border is added. */
            height: 22px;
            width: 22px;
          }
        }
      }

      .more-color-picker-wrapper {
        display: flex;
        position: relative;

        .more-color-picker {
          border-radius: 50%;
          cursor: pointer;
          height: 24px;
          width: 24px;

          &:hover {
            background-color: #E8F1FE;
          }
        }

        .color-picker-tooltip {
          position: absolute;
          bottom: 28px;
        }
      }
    
      .canvas-tool {
        border-radius: 50%;
        cursor: pointer;
        height: 18px;
        padding: 3px;
        width: 18px;
    
        &.selected {
          background-color: #D3E5F9;
        }
    
        &:not(.selected):hover {
          background-color: #E8F1FE;
        }
      }
    }
  }
}

.submission-area {
  align-items: center;
  border: 1px #9c9c9c solid;
  border-radius: 4px;
  display: flex;
  /* Height of the canvas + height of the drawing tool section. */
  height: 340px;
  justify-content: center;
  /* Width of the canvas. */
  width: 300px;

  .golden-image {
    max-width: 70%;
    max-height: 70%;
  }
}

.button-style {
  background-color: #1070fc;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  font-weight: 600;
  padding: 12px 20px;

  &:hover {
    background-color: #0d5cd7;
  }
}

.single-prompt {
  background-color: white;
  border: 1px #9c9c9c solid;
  border-radius: 4px;
  padding: 15px 20px;

  .prompt-name {
    font-size: 22px;
    font-weight: 600;
    padding-bottom: 10px;
  }

  .canvas-and-comparison {
    display: flex;
    flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
  }
}
.game-page {

  .back-button {
    border-radius: 50%;
    height: 30px;
    left: 0px;
    margin: 15px;
    padding: 5px;
    position: absolute;
    top: 0px;
    width: 30px;

    &:hover {
      cursor: pointer;
      background-color: #B7D4F5;
    }
  }

  .prompt-list {
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
    padding-bottom: 20px;
  }
  
  .completion-section {
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    padding-bottom: 30px;
  
    &.hidden {
    visibility: hidden;
    }
  
    .thanks {
    font-size: 24px;
    font-weight: 500;
    }
  }
}
  
.landing-page {

  .topic-buttons {
    align-items: center;
    background-color: #EDF4FD;
    border: #9c9c9c;
    border-radius: 8px;
    box-shadow: inset 0 0 10px #9c9c9c;
    color: #5C5C5C;
    display: flex;
    grid-gap: 32px;
    gap: 32px;
    justify-content: center;
    margin-top: 30px;
    padding: 40px
  }
}

.image-button-style {
  align-items: center;
  background-color: #D3E5F9;
  border-radius: 8px;
  box-shadow: -5px 5px 5px #9c9c9c;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  font-weight: 600;
  font-size: 24px;
  grid-gap: 20px;
  gap: 20px;
  height: 300px;
  justify-content: center;
  margin: 5px;
  padding: 20px;
  transition: all .2s ease;
  width: 300px;

  &:hover {
    box-shadow: -5px 5px 15px #9c9c9c;
    -webkit-transform: scale(1.01);
            transform: scale(1.01)
  }

  .button-image {
    max-height: 70%;
    max-width: 70%;
  }

  .button-text {
    height: 1em;
    max-width: 70%;
  }
}
body {
  background-color: #D3E5F9;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  height: 100%;
  width: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, #root {
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.error-page {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.page {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  text-align: center;
  width: 100%;
}

.page-title {
  font-size: 32px;
  font-weight: 500;
  padding: 20px;

  .page-subtitle {
    font-size: 18px;
    font-weight: 300;
    padding-top: 5px;
  }
}

